'use client';
import React, { useEffect } from 'react';
import { useParams } from 'next/navigation'
import { fetchBookId } from '@/API/booksApi';
import {
    Accordion,
    AccordionContent,
    AccordionItem,
    AccordionTrigger,
  } from "@/components/ui/accordion"
  

export default function BookDescription() {
  const params = useParams<{ id : string}>()

  const [book, setBook] = React.useState<any>(null);
  
  const fetchBookById = async () => {
    try {
      const bookData = await fetchBookId(params.id);
      setBook(bookData);
      console.log(bookData)
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(()=> {
    fetchBookById()
  },[])


  const cutDate = (date:string) =>{
    return date.substring(0,5)
  }

  return (
    <main className="min-h-screen w-full flex items-center justify-center bg-background flex-col gap-5 px-28">

    <div className='flex flex-direction-row justify-between gap-8'>
        <div className='w-1/3'>
            <img src={book?.attributes.cover} alt={book?.attributes.title} className="w-full h-full" />
        </div>
        <div className='w-2/3'>
            <h1 className='text-2xl'>{book?.attributes.title}</h1>
            <h1 className='text-xl  text-gray-500 '>{book?.attributes.author}</h1>

            <div className='my-5'>
            <h1 className='text-l  text-gray-500 '>Pages : {book?.attributes.pages}</h1>
            <h1 className='text-l  text-gray-500 '>Relased : {cutDate(book?.attributes.release_date)}</h1>
            </div>
            <p>{book?.attributes.summary}</p>

            <div className='my-4'>
            <h1 className='text-2xl my-5'>Chapters</h1>
            <div>
            {/* <Accordion type="single" collapsible>
            <AccordionItem value="item-1">
                <AccordionTrigger>Is it accessible?</AccordionTrigger>
                <AccordionContent>
                Yes. It adheres to the WAI-ARIA design pattern.
                </AccordionContent>
            </AccordionItem>
            </Accordion> */}

            </div>
            </div>
        </div>
        
    </div>
    </main>
  );
}
